a {
  text-decoration: none;
}

.header {
  display: flex;
  /* border: 1px solid red; */
  align-items: center;
  justify-content: space-around;
  /* gap: 40px; */
  background-color: #f2f6ff;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  padding-left: 20px;
  height: 60px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
}
.name {
  padding-right: 30%;
  font-weight: bold;
  color: blueviolet;
  font-size: 20px;
}

.heading {
  font-weight: bold;
}

.lastheading {
  font-weight: bold;

  padding-right: 50px;
}

.resume {
  font-weight: bold;
  color: black;

  /* border: 1px solid green; */
}

.ThirdNav {
  /* border: 1px solid red; */
  height: 50px;
  display: none;
  background-color: rgb(89, 89, 89);
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}

@media (min-width: 411px) and (max-width: 820px) {
  .header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    height: 60px;
  }
}

@media only screen and (max-width: 410px) {
  .header {
    display: none;
  }
  .ThirdNav {
    display: block;
  }

  .menu {
    /* border: 1px solid blue; */
    color: teal;
    border: none;
    /* width: fit-content; */

    /* border: 1px solid green; */
    display: flex;
    margin-left: 80%;
    padding: 5px;
    margin-right: 5%;
    margin-top: 10px;
    justify-content: center;

    align-items: center;
  }
}
