.home {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #c72040;
  /* padding-top: 100px;
  padding-bottom: 100px; */
  /* padding:5% ; */
  margin-top: 60px;
  padding-top: 50px;
  padding-bottom: 50px;
}

.home > div {
  border: 1px solid rgb(0, 30, 255);
}

.homediv {
  /* 
margin-top: 100px;
margin-left: 100px; */
  /* justify-content: left; */
  /* line-height: 30x; */
}

.homeimage {
  /* margin-top: 100px; */
  /* margin-left: 600px; */
}

.image {
  border-radius: 50%;
  height: 350px;
}

.div {
  /* border: 1px solid red; */
  width: 120px;
  background-color: blue;
  border-radius: 5px;
  padding: 10px;
  color: white;
  margin-left: 60px;
}

.a {
  text-decoration: none;
}

@media (min-width: 411px) and (max-width: 820px) {
  .home {
   flex-direction: column;
   justify-content: center;
   align-items: center;
  }

  .image {
    border-radius: 50%;
    height: 350px;
    /* margin-right: 100px; */
  }
}

@media (min-width: 0px) and (max-width: 410px) {
  .homediv {
    font-size: 10px;
    /* margin-top: 350px; */
  }

  .homeimage {
    /* border: 1px solid red; */
    /* margin-top: -50px; */
    /* margin-left: -200px; */
    width: 100px;
  }

  .image {
    height: 200px;
  }

  .div {
    margin-left: 10px;
  }
}
